.color-item {
  display: flex;
  flex-direction: column;
  justify-content: left;
  position: absolute;

  &-button {
    border: none;
    border-radius: 100%;
    box-shadow: 0 0 10px #b0b0b0d6;
    width: 30px;
    height: 30px;
    background-color: #ffffff9b;
  }

  &-button:hover {
    background-color: #ffffff;
    width: 40px;
    height: 40px;
    transition: 0.2s;
    font-size: 1rem;
  }
}

.color-name {
  margin-top: 160px;
}
